.markdown-body {
  font-family: Helvetica, arial, sans-serif;
  font-size: 14px;
  line-height: 1.6;
  background-color: white;
  padding: 30px;
}

.markdown-body > *:first-child {
  margin-top: 0 !important; }
.markdown-body > *:last-child {
  margin-bottom: 0 !important; }

.markdown-body a {
  color: #4183C4; }

.markdown-body h1,
.markdown-body h2,
.markdown-body h3,
.markdown-body h4,
.markdown-body h5,
.markdown-body h6 {
  margin: 20px 0 10px;
  padding: 0;
  font-weight: bold;
  -webkit-font-smoothing: antialiased;
  cursor: text;
  position: relative; }

.markdown-body h1 tt,
.markdown-body h1 code {
  font-size: inherit; }

.markdown-body h2 tt,
.markdown-body h2 code {
  font-size: inherit; }

.markdown-body h3 tt,
.markdown-body h3 code {
  font-size: inherit; }

.markdown-body h4 tt,
.markdown-body h4 code {
  font-size: inherit; }

.markdown-body h5 tt,
.markdown-body h5 code {
  font-size: inherit; }

.markdown-body h6 tt,
.markdown-body h6 code {
  font-size: inherit; }

.markdown-body h1 {
  font-size: 28px;
  color: black; }

.markdown-body h2 {
  font-size: 24px;
  border-bottom: 1px solid #cccccc;
  color: black; }

.markdown-body h3 {
  font-size: 18px; }

.markdown-body h4 {
  font-size: 16px; }

.markdown-body h5 {
  font-size: 14px; }

.markdown-body h6 {
  color: #777777;
  font-size: 14px; }

.markdown-body p,
.markdown-body blockquote,
.markdown-body ul,
.markdown-body ol,
.markdown-body dl,
.markdown-body li,
.markdown-body table,
.markdown-body pre {
  margin: 15px 0; }

.markdown-body hr {
  background: transparent url(data:image/png;base64,) repeat-x 0 0;
  border: 0 none;
  color: #cccccc;
  height: 4px;
  padding: 0;
}

.markdown-body > h2:first-child {
  margin-top: 0;
  padding-top: 0; }
.markdown-body > h1:first-child {
  margin-top: 0;
  padding-top: 0; }
  .markdown-body > h1:first-child + h2 {
    margin-top: 0;
    padding-top: 0; }
.markdown-body > h3:first-child, .markdown-body > h4:first-child, .markdown-body > h5:first-child, .markdown-body > h6:first-child {
  margin-top: 0;
  padding-top: 0; }

.markdown-body a:first-child h1,
.markdown-body a:first-child h2,
.markdown-body a:first-child h3,
.markdown-body a:first-child h4,
.markdown-body a:first-child h5,
.markdown-body a:first-child h6 {
  margin-top: 0;
  padding-top: 0; }

.markdown-body h1 p,
.markdown-body h2 p,
.markdown-body h3 p,
.markdown-body h4 p,
.markdown-body h5 p,
.markdown-body h6 p {
  margin-top: 0; }

.markdown-body li p.first {
  display: inline-block; }
.markdown-body li {
  margin: 0; }
.markdown-body ul,
.markdown-body ol {
  padding-left: 30px; }

.markdown-body ul :first-child,
.markdown-body ol :first-child {
  margin-top: 0; }

.markdown-body dl {
  padding: 0; }

.markdown-body dl dt {
  font-size: 14px;
  font-weight: bold;
  font-style: italic;
  padding: 0;
  margin: 15px 0 5px; }
.markdown-body dl dt:first-child {
  padding: 0; }
.markdown-body dl dt > :first-child {
  margin-top: 0; }
.markdown-body dl dt > :last-child {
  margin-bottom: 0; }
.markdown-body dl dd {
margin: 0 0 15px;
padding: 0 15px; }
.markdown-body dl dd > :first-child {
  margin-top: 0; }
.markdown-body dl dd > :last-child {
  margin-bottom: 0; }

.markdown-body blockquote {
  border-left: 4px solid #dddddd;
  padding: 0 15px;
  color: #777777; }
.markdown-body blockquote > :first-child {
  margin-top: 0; }
.markdown-body blockquote > :last-child {
  margin-bottom: 0; }

.markdown-body table {
  padding: 0;border-collapse: collapse; }
.markdown-body table tr {
  border-top: 1px solid #cccccc;
  background-color: white;
  margin: 0;
  padding: 0; }
.markdown-body table tr:nth-child(2n) {
  background-color: #f8f8f8; }
.markdown-body table tr th {
  font-weight: bold;
  border: 1px solid #cccccc;
  margin: 0;
  padding: 6px 13px; }
.markdown-body table tr td {
  border: 1px solid #cccccc;
  margin: 0;
  padding: 6px 13px; }
.markdown-body table tr th :first-child, table tr td :first-child {
  margin-top: 0; }
.markdown-body table tr th :last-child, table tr td :last-child {
  margin-bottom: 0; }

.markdown-body img {
  max-width: 100%; }

.markdown-body code,
.markdown-body tt {
  margin: 0 2px;
  padding: 0 5px;
  white-space: nowrap;
  border: 1px solid #eaeaea;
  background-color: #f8f8f8;
  border-radius: 3px; }

.markdown-body pre code {
  margin: 0;
  padding: 0;
  white-space: pre;
  border: none;
  background: transparent; }

.markdown-body pre {
  background-color: #f8f8f8;
  border: 1px solid #cccccc;
  font-size: 13px;
  line-height: 19px;
  overflow: auto;
  padding: 6px 10px;
  border-radius: 3px; }
.markdown-body pre code,
.markdown-body pre tt {
  background-color: transparent;
  border: none; }

.markdown-body sup {
  font-size: 0.83em;
  vertical-align: super;
  line-height: 0;
}
.markdown-body * {
	-webkit-print-color-adjust: exact;
}
@media print {
	.markdown-body table,
  .markdown-body pre {
		page-break-inside: avoid;
	}
	.markdown-body pre {
		word-wrap: break-word;
	}
}



