*,*::before,*::after{box-sizing:border-box}html{font-size:100px;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;text-size-adjust:100%;height:100%}body{background-color:#fdf6e3;color:#657b83;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Hiragino Sans GB","Microsoft Yahei",Arial,Helvetica,STHeiti,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";font-size:.16rem;line-height:1.5;margin:0;height:100%}h1,h2,h3,h4,h5,h6{font-weight:600;margin:0}h1{font-size:.3rem}h2{font-size:.24rem}h3{font-size:.2rem}h4{font-size:.18rem}h5{font-size:.16rem}h6{font-size:.14rem}p,blockquote,table,hr,ul,ol{margin:0}a{color:#b58900;text-decoration:underline}b,strong{font-weight:600}img{max-width:100%}blockquote{border-left:0.05rem solid #ddd;color:#777;padding-left:15px}table{border:0;border-collapse:collapse;border-spacing:0;width:100%}th,td{border:0;border-bottom:1px solid #ddd;padding:.05rem;text-align:left}th{background-color:rgba(0,0,0,0.05);font-weight:600}form{margin:0}a,button,input,select,textarea,label{outline:0;-webkit-tap-highlight-color:rgba(0,0,0,0.1);-ms-touch-action:manipulation;touch-action:manipulation}button,select,textarea,input{background-color:#fff;border:1px solid #ddd;border-radius:3px;color:#333;font-size:.16rem;padding:5px 10px}button:focus,select:focus,textarea:focus,input:focus{border-color:#39f}button{background-color:#f3f3f3;cursor:pointer}[hidden]{display:none}[disabled]{background-color:#f3f3f3;color:#777;cursor:not-allowed}hr{border:0;border-top:1px solid #ddd}#BODY_WRAP{position:relative;height:100%;margin:20px 40px}#VIEWS{margin-left:18%;min-height:100%}



